/* src/styles.css */

:root {
  --primary-green: #2E7D32;
  --primary-green-dark: #1E5E20; /* <-- LÄGG TILL DENNA RAD */
  --light-green-bg: #E8F5E9;
  /* ...resten av dina variabler... */
}

/* -- 2. Grundstilar -- */
body {
  margin: 0;
  font-family: var(--font-body);
  background-color: var(--light-green-bg); /* Behåll denna */
  color: var(--dark-text);
  
  /* Raderna för background-image och background-repeat är nu borttagna */
}
/* -- Uppdaterade stilar för Header -- */

/* Ta bort den gamla centreringen och sätt padding till 0 */
.app-header {
  padding: 0;
  text-align: left;
}

/* En ny container för att kunna justera innehållet med flexbox */
.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

/* Stil för vår nya knapp i headern */
a.header-button {
  background-color: var(--primary-green-dark); /* Mörkgrön som standard */
  color: white !important;
  padding: 10px 20px;
  border-radius: 50px;
  text-decoration: none;
  font-weight: bold;
  font-family: var(--font-header);
  transition: background-color 0.2s ease;
}

a.header-button:hover {
  background-color: var(--primary-green); /* Ljusare grön vid hover */
  text-decoration: none;
  color: white !important;
}
/* -- Lekplats-Kort (ny design inspirerad av bilden) -- */
.lekplats-kort {
  position: relative; /* Nödvändigt för att kunna positionera texten ovanpå */
  display: block;
  height: 250px; /* Ge korten en fast höjd */
  border-radius: 15px;
  overflow: hidden; /* Gömmer allt som sticker utanför de runda hörnen */
  text-decoration: none;
  color: white;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
}

/* Den mörka toningen som ligger över bilden */
.lekplats-kort::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 50%);
  opacity: 1;
  transition: opacity 0.3s ease;
}

.lekplats-kort-bild {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ser till att bilden alltid täcker ytan snyggt */
  transition: transform 0.4s ease;
}

/* Zooma in bilden lite när man hovrar över kortet */
.lekplats-kort:hover .lekplats-kort-bild {
  transform: scale(1.05);
}

.lekplats-kort-info {
  position: absolute; /* Lägg texten ovanpå bilden */
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px;
  z-index: 2; /* Se till att texten ligger över toningen */
}

.lekplats-kort-info .namn {
  font-size: 1.5rem;
  margin: 0;
  color: white; /* Se till att rubriken är vit */
  transform: translateY(0); /* Startposition */
  transition: transform 0.3s ease;
}

/* Adressen är osynlig från början */
.lekplats-kort-info .adress {
  margin: 5px 0 0 0;
  opacity: 0;
  transform: translateY(10px); /* Startposition lite längre ner */
  transition: opacity 0.3s ease, transform 0.3s ease;
}

/* ---- Hovringseffekten ---- */
/* När man hovrar över kortet... */
.lekplats-kort:hover .adress {
  opacity: 1; /* ...visa adressen */
  transform: translateY(0); /* ...och flytta den till sin rätta position */
}

/* Flytta upp namnet lite för att göra plats för adressen */
.lekplats-kort:hover .namn {
  transform: translateY(-5px);
}

.lekplats-grid {
  display: grid;
  
  /* Start med en kolumn. Detta är vår "mobil-först"-inställning. */
  grid-template-columns: 1fr; 
  
  /* Sätter avståndet mellan korten, både horisontellt och vertikalt. */
  gap: 20px; 
}

/* MEDDELANDE TILL WEBLÄSAREN: 
   "När skärmen är MINST 600px bred, använd följande regler istället" */
@media (min-width: 600px) {
  .lekplats-grid {
    /* Byt till två jämna kolumner */
    grid-template-columns: repeat(2, 1fr);
  }
}

/* MEDDELANDE TILL WEBLÄSAREN: 
   "När skärmen är MINST 900px bred, använd följande regler istället" */
@media (min-width: 900px) {
  .lekplats-grid {
    /* Byt till tre jämna kolumner, precis som du ville ha! */
    grid-template-columns: repeat(3, 1fr);
  }
}

/* -- 7. Stilar för Formulär (Uppdaterad) -- */

.form-container {
  /* 1. Bakgrunden sticker ut mer */
  background-color: rgba(255, 255, 255, 0.95); /* Lite genomskinlig vit för djup */
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 8px 25px rgba(0,0,0,0.12); /* Kraftigare skugga */
  max-width: 700px;
  margin: 30px auto;
  text-align: left;
}

/* 2. Ändra textfärgen till grön */
.form-container h2, 
.form-container h3 {
  margin-top: 0;
  margin-bottom: 20px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
  color: var(--primary-green); /* Grön färg */
}

.form-container label {
  display: block;
  font-weight: bold;
  margin-bottom: 8px;
  font-family: var(--font-header);
  color: var(--primary-green); /* Grön färg */
}

/* 3. Ram runt Utrustning och Faciliteter */
.fieldset {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.fieldset h3 {
  margin-top: 0;
  border-bottom: none; /* Ta bort dubbla linjer */
  padding-bottom: 0;
}

/* Behåll stilar för input-fälten */
.form-container input[type="text"],
.form-container input[type="number"],
.form-container textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-sizing: border-box;
  font-family: var(--font-body);
  font-size: 1rem;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.form-container input[type="text"]:focus,
.form-container input[type="number"]:focus,
.form-container textarea:focus {
  outline: none;
  border-color: var(--primary-green);
  box-shadow: 0 0 0 3px rgba(46, 125, 50, 0.2);
}

.checkbox-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.checkbox-group {
  display: flex;
  align-items: center;
}

.checkbox-group input[type="checkbox"] {
  margin-right: 10px;
  width: 20px;
  height: 20px;
}

/* 4. Gör så att knapparna är mörkgröna från början */
.form-container button[type="submit"] {
  width: 100%;
  padding: 15px;
  background-color: var(--primary-green-dark); /* Mörkgrön som standard */
  color: white;
  border: none;
  border-radius: 8px;
  font-family: var(--font-header);
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.form-container button[type="submit"]:hover {
  background-color: var(--primary-green); /* Ljusare grön vid hover */
}

/* -- 8. Stilar för Lekplats Detaljsida -- */

.detail-container {
  max-width: 900px;
  margin: 20px auto;
}

.detail-header-image {
  width: 100%;
  height: 400px;
  object-fit: cover;
  border-radius: 15px;
  margin-bottom: 20px;
}

.detail-title {
  text-align: center;
  margin-bottom: 30px;
}

.info-box {
  background-color: rgba(255, 255, 255, 0.95);
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.08);
  display: grid;
  grid-template-columns: 1fr 1fr; /* Två kolumner */
  gap: 20px;
  margin-bottom: 30px;
}

.info-item {
  display: flex;
  align-items: center;
  font-size: 1.1rem;
}

.info-item .icon {
  font-size: 1.5rem;
  color: var(--primary-green);
  margin-right: 15px;
}

.star-rating .filled-star {
  color: #f5c518;
}

.star-rating .empty-star {
  color: #ccc;
}

/* Stil för bildkarusellen */
.carousel .slide img {
  border-radius: 15px;
  max-height: 500px; /* Förhindra att bilderna blir för höga */
  object-fit: contain;
}
.carousel .control-arrow, .carousel.carousel-slider .control-arrow {
  opacity: 0.7;
}

/* Stil för inchecknings-flödet */
.checkin-feed {
  margin-top: 40px;
}

.checkin-card {
  background-color: #fff;
  border-radius: 15px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
}

.checkin-card-header {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.checkin-card-header strong {
  margin-right: 10px;
}

.checkin-card-header span {
  font-size: 0.9rem;
  color: #888;
}